<template>
  <div id="time">
    <div v-if="time.length > 0">
      <p>Vida: {{ this.vida }}</p>
      <p>Ataque: {{ this.ataque }}</p>
      <div>
          <img
            v-bind:alt="c.hero"
            class="center-cropped"
            :src="getImgUrl(c.hero)"
            @click="removeFilosofo(index)"
            v-for="(c, index) in time"
            :key="index"/>
      </div>
      <br /><br />
    </div>
    <div v-else>
      <p>Não tem ninguem no seu time ainda</p>
      <br /><br /><br /><br />
    </div>
  </div>
</template>

<script>
export default {
  name: 'TeamCards',
  props: {
    time: Array,
    vida: Number,
    ataque: Number,
  },
  methods: {
		getImgUrl(pic) {
			return require("../assets/" + pic.substring(0, pic.length -4) + ".png");
		},
    removeFilosofo(index) {

      this.$emit('vender-filosofo', index);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
