<!-- Use preprocessors via the lang attribute! e.g. <template lang="pug"> -->
<template>
	<div id="app">
		<h1>{{ title }}</h1>

		<ShopBox
			:filosofos="filosofos"
			@comprar-filosofo="$value => comprarFilosofo($value)"
		></ShopBox>

		<TeamCards
			:time="time"
			:vida="vida"
			:ataque="ataque"
			@set-time="$value => time = $value"
			@vender-filosofo="$value => venderFilosofo($value)"
		></TeamCards>

		<div id="msgs">
			<div v-if="msgs.length > 0">
				<p v-for="(c, index) in msgs" :key="index">{{c}}</p>
			</div>
		</div>

		<div id="dragonLair">
			<div v-if="vidaDragao > 0">
				<p>DRAGAO!!!!!</p>
				<p>{{ this.vidaDragao }}</p>
				<img alt="graaaaurrrr" src="./assets/dragao.png" />
			</div>
			<div v-else>
				
				<div v-if="cabou == 0" >
					<p>Daqui a pouco o dragão chega</p>
					<br />
					<button id="dragao" @click="chamarDragao()">CHAMA O DRAGÃO</button>
				</div>
				<div v-else>
					<button @click="recomecar()">recomeçar</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import TeamCards from './components/TeamCards.vue'
import ShopBox from './components/ShopBox.vue'

export default {
	components: {
		TeamCards,
		ShopBox,
	},
	data() {
		return {
			filosofos: [
				{
					hero: "Lula 4/3",
					msg: "LULA 2022",
					buff: 0,
					vida: 4,
					ataque: 3
				},
				{
					hero: "Locke 3/4",
					msg: "Locke: Sempre considerei as ações dos homens como as melhores intérpretes dos seus pensamentos",
					buff: 0,
					vida: 3,
					ataque: 4
				},
				{
					hero: "Marx 1/2",
					msg: "Filósofos do mundo, Uni-vos!! Marx buffa o ataque do time em 2",
					buff: 1,
					vida: 1,
					ataque: 1
				},
				{
					hero: "Pizzaiolo 1/1",
					msg: "Pizzaiolo tira 1 de ataque do dragão",
					buff: 2,
					vida: 1,
					ataque: 2
				},
			],
			time: [],
			msgs: ["Monte seu time"],
			vida: 0,
			ataque: 0,
			vidaDragao: 0,
			ataqueDragao: 10,
			title: "Batalha dos políticos!",
			cabou: 0,
			isPorrada: false
		};
	},
	methods: {
		newMsg(msg){
			if(this.msgs.length == 2){
				this.msgs.shift();			
			}
			this.msgs.push(msg);
		},
		recomecar(){
			this.time = [];
			this.msgs = ["Monte seu time"];
			this.cabou = 0;
			this.vida = 0;
			this.ataque = 0;
			this.ataqueDragao = 10;
		},
		comprarFilosofo(filosofo) {
			let msg = filosofo.msg;
			let buff = filosofo.buff;
			let vida = filosofo.vida;
			let ataque = filosofo.ataque;
			if (this.time.length < 5) {
				this.time.push(filosofo);
				this.vida = this.vida + vida;
				this.ataque = this.ataque + ataque;
				this.newMsg(msg);
				if (buff == 1) {
					this.ataque = this.ataque + 2;
				}
				if (buff == 2) {
					this.ataqueDragao = this.ataqueDragao - 1;
				}
			} else {
				this.newMsg("chega de filosofos! Chame o dragão");
			}
		},
		venderFilosofo(index) {
			let filosofo = this.time.filter((_, i) => i == index)[0];
			console.log(filosofo);
			console.log(filosofo["vida"]);
			this.time = this.time.filter((_, i) => i != index)
			console.log("maoe");
console.log(this.time);
			this.vida = this.vida - filosofo.vida;
			this.ataque = this.ataque - filosofo.ataque;
		},
		chamarDragao() {
			this.vidaDragao = 50;
			this.msgs = [];
			this.isPorrada = true;
		},
		porradaTick() {
			this.newMsg("A porrada tá rolando!");
			this.vidaDragao = this.vidaDragao - this.ataque;
			this.vida = this.vida - this.ataqueDragao;
			if (this.vidaDragao <= 0 && this.vida <= 0) {
				this.vidaDragao = 0;
				this.newMsg("VOCE EMPATOU");
				this.cabou = 1;
				this.isPorrada = false;
			} else if (this.vidaDragao <= 0) {
				this.vidaDragao = 0;
				this.newMsg("VOCE VENCEU");
				this.cabou = 1;
				this.isPorrada = false;
			} else if (this.vida <= 0) {
				this.vidaDragao = 0;
				this.newMsg("VOCE PERDEU");
				this.cabou = 1;
				this.isPorrada = false;
			}
		}
	},
	created() {
		setInterval(()=>{
			if (this.isPorrada) {
				this.porradaTick();
			}
		}, 1000)
	}
};
</script>

