<template>
  <div id="shop">
		<button 
      v-for="(c, index) in filosofos"
      :key="index"
      :id="c.hero"
      @click="comprarFilosofo(c)">Comprar {{c.hero}}
    </button>
  </div>
</template>

<script>
export default {
  name: 'ShopBox',
  props: {
    filosofos: Array
  },
  methods: {
		comprarFilosofo(filosofo) {
			this.$emit('comprar-filosofo', filosofo);
		}
  }
}
</script>

